.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover {
    /* background: transparent; */
    /* border-color: #000000; */
    box-shadow: 0 0 11px #37E2D5;
    cursor: default;
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: #151b4e;
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 3rem;
}

.experience__details {
    display: flex;
    gap: 1rem;
}

.experience__details h4, .experience__details small{
    color: #000000;
}

.experience__details-icon {
    margin-top: 6px;
    color: #151b4e;
}

@media screen and (max-width: 1024px) {
    .experience__container {
      grid-template-columns: 1fr;
    }
    .experience__container > div {
        width: 80%;
        padding: 2rem;
        margin: 0 auto;
    }

    .experience__content {
        padding: 1rem;
    }

  }
  
  @media screen and (max-width: 600px) {
    .experience__container {
        gap: 1rem;
      }
    .experience__container > div {
        width: 100%;
        padding: 2rem 1rem;
    }
  }