.p {
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid rgb(214, 213, 213);
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
  }
  
  .p-browser {
    height: 20px;
    background-color: rgb(214, 213, 213);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
  }
  
  .p-circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background: black;
  }
  
  .p-img {
    width: 100%;
    transition: all 10s ease;
  }
  
  .p:hover .p-img {
    transform: translateY(-70%);
  }

  @media screen and (max-width: 1024px) {
    .p {
      width: 90%;
      height: 30vh;
    }
  }
  
  @media screen and (max-width: 600px) {
    .p {
      width: 100%;
      height: 30vh;
    }
  }